'use strict';

import Base from './base.js';

export default class extends Base {
  /**
   * index action
   * @return {Promise} []
   */
  async loginAction(){

      this.assign("title","Đăng nhập của quản trị viên")
      return this.displayView('admin_login');
  }
  async redirectAction(){
      return this.displayView('admin_redirect');
  }
  async dologinAction(){
        let data=this.post();
        let md5Pas = await think.md5(data.password);
        let uname = await data.username;
        let result=await this.model("home").findOne("user",{name:uname,role:{"<":4}});
        let info={
              name: uname,
              password: md5Pas
        }
        if(uname===result.name&&md5Pas===result.password){
              await this.session("uInfo", info);
              return this.json({status:1,msg:"Đăng nhập thành công!"});
        }else{
            return this.json({status:0,msg:"Tên đăng nhập hoặc mật khẩu không chính xác!"});
        }
  }

}
